.nav-list {
  @include list-reset();
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 10px;
}

.nav-list__link {
  display: block;
  padding: 10px 0;
  width: 100%;

  font-size: 30px;
  font-weight: 700;
  font-style: normal;
  line-height: 1.2;
  letter-spacing: normal;
  color: $white;
  text-transform: uppercase;

  &:hover,
  &:focus {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

@media (min-width: $desktop-width) {
  .nav-list {
    flex-direction: row;
    justify-content: flex-start;
    margin-top: 20px;
  }

  .nav-list__item:not(:last-child) {
    margin-right: 30px;
  }
}
